import React from "react";
import "../../../../index.scss";
import "./CertificateOrder.scss";
import AppContext from "../../../../context";
import CertificateDesign from "../certificateDesign";

function CertificateOrder() {
  const { certificateDesign, setCertificateDesign } =
    React.useContext(AppContext);
  return (
    <>
      {certificateDesign && (
        <CertificateDesign certificateDesign={certificateDesign} />
      )}
      {!certificateDesign && (
        <div className="container_no-top certificate--order">
          <ol>
            <li>  </li>
            <li>
                  e-mail  5   
            </li>
            <li>   22.04.2022</li>
            <li>     </li>
            <li className="li_none">
              <button
                className="btn btn-link btn-link_left"
                onClick={() => setCertificateDesign(!certificateDesign)}
              >
                  ?
              </button>
            </li>
          </ol>
        </div>
      )}
    </>
  );
}
export default CertificateOrder;
