import { useContext, useState } from "react";
import { useTransition, animated } from "react-spring";

import AppContext from "../../context";
import styles from "./Popup.module.scss";
import data from "../../JSON/main.json";

function Popup() {
  const { modal, setModal, setStep, setFaqScreen, screen } =
    useContext(AppContext);

  const transition = useTransition(!modal, {
    from: { opacity: 0, transform: "translateY(100%)" },
    enter: { opacity: 1, transform: "translateY(0)" },
  });

  const [offSeasonBool, setOffSeasonBool] = useState(data.offSeason);

  return (
    <>
      {transition(
        (style, item) =>
          item && (
            <animated.div style={style} className={styles.widgetFixed}>
              {data.offSeason && offSeasonBool && (
                <>
                  <div
                    className={styles.widgetFixedOffSeasonBtn}
                    onClick={() => {
                      setOffSeasonBool(false);
                    }}
                  >
                    <svg
                      width="13"
                      height="13"
                      viewBox="0 0 13 13"
                      fill="none"
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <path
                        d="M12 0.5L6.25 6.25M0.5 12L6.25 6.25M6.25 6.25L12 12L0.5 0.5"
                        stroke="black"
                      ></path>
                    </svg>
                  </div>
                  <div className={styles.widgetFixedOffSeason}>
                    <div>
                      <div className={styles.widgetFixedTitle}>
                         !
                      </div>
                          <span>01  2022 .</span>
                    </div>
                    <div>
                      <div className={styles.widgetFixedTitle}>
                         :
                      </div>
                      <ol>
                        <li>  </li>
                        <li>
                               
                          
                        </li>
                      </ol>
                    </div>
                  </div>
                </>
              )}
              <div
                className={styles.widgetFixedInner}
                style={{
                  boxShadow: "0px -5px 15px 5px rgba(34, 60, 80, 0.06)",
                }}
              >
                <div className={styles.widgetFixedBox}>
                  {screen.width <= 500 && (
                    <a
                      href={`tel:${data.phoneHref}`}
                      className={`${styles.widgetFixedButton} ${styles.widgetFixedButtonChatStyle}`}
                    >
                      {data.phoneFormated}
                    </a>
                  )}
                  <button
                    className={`${styles.widgetFixedButton} ${styles.widgetFixedButtonStyled}`}
                    onClick={() => {
                      setModal(!modal);
                      setStep("selfTimes");
                    }}
                  >
                     
                  </button>
                  <button
                    className={
                      screen.width >= 500
                        ? `${styles.widgetFixedButton}`
                        : `${styles.widgetFixedButton} ${styles.widgetFixedButtonSecondary}`
                    }
                    onClick={() => {
                      setModal(!modal);
                      setStep("giftCertificateOrder");
                    }}
                  >
                     
                  </button>
                  {screen.width >= 500 && (
                    <button
                      className={styles.widgetFixedButton}
                      onClick={() => {
                        setModal(!modal);
                        setFaqScreen(true);
                      }}
                    >
                        
                    </button>
                  )}
                </div>
              </div>
              {data.chatOnline && screen.width >= 500 && (
                <div className={styles.widgetFixedBoxChat}>
                  <button
                    className={`${styles.widgetFixedButton} ${styles.widgetFixedButtonChatStyle}`}
                  >
                      
                  </button>
                </div>
              )}
            </animated.div>
          )
      )}
    </>
  );
}

export default Popup;
