import React, { useState } from "react";
import { useTransition, animated } from "react-spring";
import Slider from "react-slick";


function Content({ styles, item, index }) {
  const [textShow, setTextShow] = useState(false);
  const [picturesShow, setPicturesShow] = useState(false);
  React.useEffect(() => {
    if (index === 0) {
      setPicturesShow(true);
    }
  }, [index]);

  const text = useTransition(textShow, {
    from: { opacity: 0, transform: "translateX(-100%)" },
    enter: { opacity: 1, transform: "translateX(0)" },
  });

  function SampleNextArrow(props) {
    const { className, onClick } = props;
    return (
      <button className={`${className} ${styles.slickNext}`} onClick={onClick}>
        
      </button>
    );
  }

  function SamplePrevArrow(props) {
    const { className, onClick } = props;
    return (
      <button className={`${className} ${styles.slickPrev}`} onClick={onClick}>
        
      </button>
    );
  }

  const settings = {
    lazyLoad: true,
    infinite: true,
    slidesToShow: 1,
    slidesToScroll: 1,
    variableWidth: true,
    initialSlide: 2,
    nextArrow: <SampleNextArrow />,
    prevArrow: <SamplePrevArrow />,
  };

  return (
    <>
      {text(
        (style, obj) =>
          obj && (
            <animated.div style={style} className={styles.detailText}>
              <div>
                <p>
                  <b> </b> {item.availableFrom} 
                </p>
                <p>
                  <b> </b> {item.passengerFrom} 
                </p>
                <p>
                  <b>:</b> {item.restrictions}
                </p>
                <p>
                  <b>:</b> {item.clothes}
                </p>
                <p>
                  <b>  :</b> {item.issuedLocally}
                </p>
              </div>
              <div style={{ whiteSpace: "pre-wrap" }}>{item.text}</div>
            </animated.div>
          )
      )}
      {picturesShow && (
        <div
          className={styles.pictures}
          style={{ overflow: "hidden" }}
          id="slick"
        >
          <Slider {...settings}>
            {item.video.map((obj) => {
              return (
                <video key={obj.src} preload muted controls playsInline>
                  <source src={obj.src} type="video/mp4"></source>
                </video>
              );
            })}
            {item.images.map((obj) => {
              return (
                <img
                  key={obj.src}
                  style={{
                    display: 'block',
                    minHeight: "50px",
                    maxWidth: "100%",
                    maxHeight: "350px",
                    marginLeft: 'auto',
                    marginRight: 'auto'
                  }}
                  src={obj.src}
                  alt="img"
                ></img>
              );
            })}
          </Slider>
        </div>
      )}
      <div className={styles.programDetailBtns}>
        <button
          type="button"
          className="btn btn-link btn-link_left"
          onClick={() => {
            setPicturesShow(false);
            setTextShow(!textShow);
          }}
        >
            
        </button>
        <button
          type="button"
          className="btn btn-link btn-link_left"
          onClick={() => {
            setTextShow(false);
            setPicturesShow(!picturesShow);
          }}
        >
            
        </button>
      </div>
    </>
  );
}
export default Content;
